<template>
    <el-checkbox-group :value="value" @input="skillChange">
        <el-checkbox v-for="(skill,index) in options" :label="skill" :key="index">{{skill}}</el-checkbox>
    </el-checkbox-group>
</template>
<script>
    export default {
        props: {
          value: Array,
        },
        data() {
            return {
                options: ['心脏病护士', '打针护士', '心脏2病护士', '打针2护士']
            }
        },
        methods: {
            skillChange(val) {
                this.$emit('input', val)
            }
        }
    }
</script>