<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="service-wrapped_1">
        <el-card class="box-card">
            <el-form :model="ruleForm" :rules="rules" label-position="left" label-width="160px">
                <div class="multiple-title-card">
                    <el-form-item label="服务产品：" prop="userName">
                        <el-input v-model="ruleForm.name" placeholder="请输入服务产品" style="width: 220px"></el-input>
                    </el-form-item>
                    <el-form-item label="服务分类：" prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="请选择服务分类">
                            <el-option
                                v-for="(v,k) in $Cfg.postType"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <div class="card-list">
                        <el-card class="card_specs" shadow="always" v-for="(n, index) in cardLen" :key="index">
                            <div class="specs">
                                <div class="specs-left">
                                    <el-form-item label="服务规格：" prop="userName">
                                        <el-input v-model="ruleForm.name" placeholder="请输入服务规格"
                                                  style="width: 220px"></el-input>
                                    </el-form-item>
                                    <el-form-item label="计时方式：" prop="userName">
                                        <el-radio-group v-model="timeUnit">
                                            <el-radio :label="3">分</el-radio>
                                            <el-radio :label="6">小时</el-radio>
                                            <el-radio :label="9">天</el-radio>
                                        </el-radio-group>
                                    </el-form-item>
                                    <el-form-item label="服务时长：" prop="userName">
                                        <el-input v-model="ruleForm.name" placeholder="请输入服务时长"
                                                  style="width: 220px"></el-input>
                                    </el-form-item>
                                    <el-form-item label="服务金额：" prop="userName">
                                        <el-input v-model="ruleForm.name" placeholder="请输入服务金额"
                                                  style="width: 220px"></el-input>
                                    </el-form-item>
                                    <el-button type="primary">添加服务规格</el-button>
                                    <el-button type="danger" v-if="cardLen > 1">删除服务规格</el-button>
                                </div>
                                <div class="specs-right">
                                    <el-input
                                        type="textarea"
                                        style="width: 480px"
                                        :autosize="{ minRows: 9}"
                                        placeholder="请输入服务详情，字数限制300"
                                        v-model="textarea2"></el-input>
                                </div>
                            </div>
                        </el-card>
                    </div>
                </div>
                <div class="multiple-title-card course-part">
                    <div class="title">课程部分（套餐内课程）</div>
                    <el-form-item prop="userName">
                        <el-radio-group v-model="timeUnit">
                            <el-radio :label="3">原子课</el-radio>
                            <el-radio :label="6">课程包</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="请选择服务用户">
                            <el-option
                                v-for="(v,k) in $Cfg.postType"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item class="u-row-table">
                        <el-row>
                            <el-col :span="8" class="info">
                                课程名称
                            </el-col>
                            <el-col :span="3" class="info">
                                操作
                            </el-col>
                        </el-row>
                        <el-row v-for="n in 3" :key="n">
                            <el-col :span="8">
                            </el-col>
                            <el-col :span="3">
                                <el-button size="small" type="danger">移除</el-button>
                            </el-col>
                        </el-row>
                    </el-form-item>
                </div>
                <div class="multiple-title-card course-part">
                    <div class="title">课程部分（套餐外课程，运营活动位置）</div>
                    <el-form-item prop="userName">
                        <el-radio-group v-model="timeUnit">
                            <el-radio :label="3">原子课</el-radio>
                            <el-radio :label="6">课程包</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="请选择服务用户">
                            <el-option
                                v-for="(v,k) in $Cfg.postType"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item class="u-row-table">
                        <el-row>
                            <el-col :span="8" class="info">
                                课程名称
                            </el-col>
                            <el-col :span="3" class="info">
                                操作
                            </el-col>
                        </el-row>
                        <el-row v-for="n in 3" :key="n">
                            <el-col :span="8">
                            </el-col>
                            <el-col :span="3">
                                <el-button size="small" type="danger">移除</el-button>
                            </el-col>
                        </el-row>
                    </el-form-item>
                </div>
                <div class="multiple-title-card">
                    <div class="title">佣金比例部分</div>
                        <div class="commission">
                            <div class="commission-item">
                                <div class="commission_title">工作室有获客者分佣情况</div>
                                <el-form-item label="工作室负责人：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="工作室护士：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="获客者：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="平台：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                            </div>
                            <div class="commission-item">
                                <div class="commission_title">工作室无获客者分佣情况</div>
                                <el-form-item label="工作室负责人：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="工作室护士：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="平台：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                            </div>
                            <div class="commission-item">
                                <div class="commission_title">个人护士分佣情况</div>
                                <el-form-item label="个人护士：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="工作室护士：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="平台：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                            </div>
                            <div class="commission-item">
                                <div class="commission_title">个人护士分佣情况</div>
                                <el-form-item label="个人护士：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                                <el-form-item label="平台：" prop="userName">
                                    <el-input v-model="ruleForm.name" placeholder="请输入分成比例" style="width: 220px">
                                        <template slot="append">%</template>
                                    </el-input>
                                </el-form-item>
                            </div>
                        </div>
                </div>
                <div class="multiple-title-card">
                    <div class="title">服务信息部分</div>
                    <el-form-item label="总服务金额：" prop="userName"></el-form-item>
                    <el-form-item label="服务周期：" prop="userName">
                        <el-input v-model="ruleForm.name" placeholder="请输入天数" style="width: 220px">
                            <template slot="append">天</template>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="技能标签：" prop="userName">
                        <SkillLabel v-model="skillArray"></SkillLabel>
                    </el-form-item>
                    <el-form-item label="指定工作室：" prop="userName">
                        <AppointStudio></AppointStudio>
                    </el-form-item>
                    <el-button type="primary">提交</el-button>
                </div>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    import SkillLabel from './SkillLable'
    import AppointStudio from './AppointStudio'
    export default {
        data() {
            return {
                ruleForm: {},
                rules: {},
                timeUnit: '',
                textarea2: '',
                cardLen: 3,
                options: [{
                    value: '选项1',
                    label: '黄金糕'
                }, {
                    value: '选项2',
                    label: '双皮奶',
                    disabled: true
                }, {
                    value: '选项3',
                    label: '蚵仔煎'
                }, {
                    value: '选项4',
                    label: '龙须面'
                }, {
                    value: '选项5',
                    label: '北京烤鸭'
                }],
                value: '',
                skillArray: []
            }
        },
        computed: {

        },
        methods: {},
        created() {

        },
        components: {
            SkillLabel,
            AppointStudio
        }
    }
</script>
